import React from 'react'
import './index.scss'

const Result = ({gameEndState}) => {
    return (
        <div>
            {/* No era pra a gente precisar checar se a string  null, pq era pra isso
            s aparecer quando j tivesse um valor na string... WTF */}
            { gameEndState ? gameEndState.split('\n').map( str => <p>{str}</p>) : null}
        </div>
    )
}

export default Result
